import {CHANGE_SELECT, DEPOSIT_CLOSE_MODAL, ON_CHANGE, OPEN_MODAL, RENDER} from "../../actions/actionsType";
import {validateControl} from "../../../Modules/formValidation";
import firebase from "firebase/app";
import "firebase/database";

// Рендер всех депозитов на страницу
export function renderDeposit() {
	return async (dispatch, getState) => {
		const db = firebase.database();
		await db.ref('/users/' + getState().authReducer.token + '/portfolio/deposit').once('value')
			.then(response => {
				const deposit = response.val();

				if (deposit === null) {
					db.ref('/users/' + getState().authReducer.token + '/portfolio').update({});
					const deposit = {};
					dispatch({type: RENDER, deposit, showPreloader: false});
					return
				}

				dispatch({type: RENDER, deposit, showPreloader: false})
			}).catch(err => console.error(err));
	}
}

// сабмит формы по созданию депозита
export function createDeposit(e) {
	e.preventDefault();

	return async (dispatch, getState) => {
		const db = firebase.database();
		const state = getState().depositReducer;
		const {date, amount, currency} = state.depositData;
		const year = new Date(date).getFullYear();
		const showPreloader = true;
		const formAddDepoInputs = {...state.formAddDepoInputs};
		const amountInput = {...formAddDepoInputs.amount};
		const url = '/users/' + getState().authReducer.token + '/portfolio/deposit/' + year + '/' + currency;

		amountInput.value = "";
		formAddDepoInputs.amount = amountInput;

		await db.ref(url).once('value')
			.then(response => {
				// Если год создаем впервые
				if (response.val() === null) {
					const data = [];

					data.push({
						amount: +amount,
						date: date,
						id: data.length + Math.random()
					});

					db.ref(url).set(data);
					const isFormValid = false;

					dispatch({type: RENDER, showPreloader, isFormValid, formAddDepoInputs});
					dispatch(renderDeposit());

					return;
				}


				const data = response.val();
				// Если уже имеем массив
				data.push({
					amount: +amount,
					date: date,
					id: response.val().length + Math.random()
				});

				console.log(state)
				console.log(data)

				db.ref(url).set(data);
				const isFormValid = false;

				dispatch({type: RENDER, showPreloader, isFormValid, formAddDepoInputs});
				dispatch(renderDeposit());

			})
			.catch(err => console.error(err));
	}
}

// Удаление депозита
export function deleteDeposit(e) {
	return async (dispatch, getState) => {
		const state = getState().depositReducer;
		const db = firebase.database();
		const showPreloader = false;
		const showModal = false;
		const data = {...state.data};
		const url = '/users/' + getState().authReducer.token + '/portfolio/deposit/' + data.year + '/' + data.currency;
		await db.ref(url).once('value')
			.then(response => {
				if (response.val().length === 1) {
					db.ref(url).remove();
					db.ref('/users/' + getState().authReducer.token + '/portfolio/deposit').update({});
					db.ref('/users/' + getState().authReducer.token + '/portfolio/deposit').once('value')
						.then(response => {
							let deposit = response.val();

							if (deposit === null) {
								deposit = {};
							}

							dispatch({type: RENDER, deposit, showPreloader, showModal});
						})
						.catch(err => console.error(err));
				} else {
					const result = response.val().filter(item => item.id !== +data.id);
					db.ref(url).set(result);

					db.ref('/users/' + getState().authReducer.token + '/portfolio/deposit').once('value')
						.then(response => {
							let deposit = response.val();
							dispatch({type: RENDER, deposit, showPreloader, showModal});
						})
						.catch(err => console.error(err));

				}
			})
			.catch(err => console.log(err));
	}
}


/* ----- START Открытие и закрытие модального окна -----*/

export function openModal(e) {
	return dispatch => {
		const data = e.currentTarget.dataset;
		const showModal = true;

		dispatch({type: OPEN_MODAL, showModal, data});
	}
}

export function closeModal() {
	return dispatch => {
		dispatch({type: DEPOSIT_CLOSE_MODAL, showModal: false});
	}
}

/* ----- END Открытие и закрытие модального окна -----*/

/* ----- START Все, что связано с событием onChange для инпутов, селектов и прочего -----*/

export function onChangeHandler(e, inputName, inputObj) {
	return (dispatch, getState) => {
		const state = getState().depositReducer;

		const depositData = {...state.depositData};
		// Клонирую объект с инпутами
		const inputs = {...state[inputObj]};
		// controlName - amount, date
		const controlName = {...inputs[inputName]};

		// Валидация
		controlName.touched = true;
		controlName.value = e.target.value;
		controlName.valid = validateControl(controlName.value, controlName.validation);

		depositData[inputName] = e.target.value;
		inputs[inputName] = controlName;

		let isFormValid = true;

		// Валидация формы (активные кнопки или нет)
		Object.keys(inputs).forEach(name => {
			isFormValid = inputs[name].valid && isFormValid;
		});

		dispatch({
			type: ON_CHANGE,
			depositData,
			[inputObj]: inputs,
			isFormValid
		})
	}
}

//TODO: Пока не понял, как работает Date, починить!
export function handleChangeDate(date) {
	return (dispatch, getState) => {
		const state = getState().depositReducer;
		const depositData = {...state.depositData};

		// NOTE: Datepicker как то криво работает, пришлось использовать Date.parse
		depositData.date = Date.parse(date);
		dispatch({type: CHANGE_SELECT, depositData});
	}
}

export function selectHandler(e) {
	return (dispatch, getState) => {
		const state = getState().depositReducer;
		const depositData = {...state.depositData};
		depositData.currency = e.target.value;

		dispatch({type: CHANGE_SELECT, depositData});
	}
}

/* ----- END Все, что связано с событием onChange для инпутов, селектов и прочего -----*/
