import React from "react";
import classes from "./CompaniesList.module.scss";

const CompaniesList = props => {

	function renderCompanies() {
		return Object.keys(props.companies).map((name, index) => {
			const company = props.companies[name];

			console.log(props.currency)
			console.log(company.currency)

			if (props.currency === company.currency) {
				return (
					<div className={classes.row} key={index}>
					<span datatype="name" className={classes.CompanyName}
						  onClick={props.showModalHandlerDelete}>{name}
					</span>
						<span datatype="count">
						{(1 * company.count)}
					</span>
						<span
							datatype="dividendPerShare">{(+company.dividendPerShare)}
					</span>
						<span>
						{Math.ceil((company.count * company.dividendPerShare * 100) / 100).toLocaleString('ru')}
					</span>
					</div>
				)
			}
		})
	}

	// Рендер футера с налогами
	function renderFooter(companies) {
		const companyArr = [];

		let beforeTax = 0;
		let afterTax = 0;

		for (let name in companies) {
			const company = companies[name];

			if (props.currency !== company.currency) continue;

			const bt = company.count * company.dividendPerShare;
			const at = bt - (bt * 0.13);

			beforeTax += bt;
			afterTax += at;
		}

		const arrItem = (
			<div key={afterTax} className={classes.footer}>
				<div className={classes.Total}>
						<span>
							Прибыль до вычета налогов
						</span>
					<span>
							{(Math.ceil((beforeTax) * 100) / 100).toLocaleString('ru')}
						</span>
				</div>

				<div className={classes.Total}>
						<span>
							Прибыль после вычета налогов
						</span>

					<span className={classes.afterTax}>
							{(Math.ceil((afterTax) * 100) / 100).toLocaleString('ru')}
						</span>
				</div>
			</div>
		);

		companyArr.push(arrItem);
		return companyArr
	}

	return (
		<div className={classes.CompaniesList}>
			<div className={classes.header}>
				<span>Имя компании</span>
				<span>Акции </span>
				<span>Дивиденд</span>
				<span>Прибыль (год)</span>
			</div>
			{
				renderCompanies()
			}
			{
				renderFooter(props.companies)
			}
		</div>
	)
};

export default CompaniesList;
