import {
	CHANGE_SELECT,
	CLOSE_MODAL,
	CREATE_COMPANY,
	CURRENCY,
	GET_COMPANY_DATA,
	OPEN_MODAL_UPDATE,
	RENDER, SHOW_PRELOADER
} from "../../actions/actionsType";
import {validateControl} from "../../../Modules/formValidation";
import firebase from "firebase/app";
import "firebase/database";


// Асинхронная операция на сервер, рендерит список компаний
export function renderCompanies() {
	return async (dispatch, getState) => {
		const state = getState().mainReducer;
		const db = firebase.database();

		//NOTE: Перед рендерингом посылаю запрос, чтобы в пустом массиве компаний создать объект без null
		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').update({});
		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').once('value')
			.then(snapshot => {
				const companies = snapshot.val();

				if (companies === null || companies === undefined) {
					dispatch({type: RENDER, showPreloader: false, companies: {}});
					return
				}

				const currency = [...state.currency];

				dispatch({type: RENDER, companies, showPreloader: false});
				// Ищу сколько всего валют, пушу в массив новые валюты, получаю массив без дубликатов
				Object.keys(companies).forEach(companyName => {
					const crc = companies[companyName].currency;
					if (!currency.includes(crc)) currency.push(crc)
				});

				dispatch({type: CURRENCY, currency})

			})
			.catch(err => console.error(err));
	}
}

// Создание компании
export function createCompany(e) {
	e.preventDefault();

	return async (dispatch, getState) => {
		const state = getState().mainReducer;
		const db = firebase.database();
		const {name, count, dividendPerShare, currency} = state.company;
		const company = create(name, count, dividendPerShare, currency);

		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').update(company);
		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').once('value')
			.then(response => {
				const modals = {...state.modals};
				const formAddInputs = {...state.formAddInputs};
				modals.isOpenModalAddCompany = false;
				const companies = response.val();

				// очищаю инпут value, чтобы после добавления компании поля были пустыми
				Object.keys(formAddInputs).forEach(name => {
					const control = {...formAddInputs[name]};
					control.value = "";
					formAddInputs[name] = control;
				});

				dispatch({type: CREATE_COMPANY, company, companies, modals, formAddInputs});
				dispatch(renderCompanies())
			}).catch(err => console.error(err));

		// Создание и пуш компании
		function create(name, count, dividendPerShare, currency) {
			return {
				[name]: {
					count,
					dividendPerShare,
					currency
				}
			}
		}

	}
}

//Собираю данные для редактирования/удаления компании (клик на имя компании)
export function clickOnCompany(e) {
	return (dispatch, getState) => {
		const state = getState().mainReducer;
		const company = Object.assign({}, state.company);
		const formUpdInputs = {...state.formUpdInputs};
		const count = {...formUpdInputs.count};
		const dividendPerShare = {...formUpdInputs.dividendPerShare};
		const parent = e.target.parentNode;

		company.name = e.target.textContent;
		company.count = count.value = parent.querySelector("span[datatype='count']").textContent;
		company.dividendPerShare = dividendPerShare.value = parent.querySelector("span[datatype='dividendPerShare']").textContent;

		formUpdInputs.count = count;
		formUpdInputs.dividendPerShare = dividendPerShare;

		dispatch({
			type: GET_COMPANY_DATA,
			company,
			formUpdInputs,
			isFormValid: true
		});

		dispatch(openModal(e, "isOpenModalUpdateCompany"));
	}
}

// Обновление данных по компаниям
export function updateCompany() {
	return async (dispatch, getState) => {
		const state = getState().mainReducer;
		const db = firebase.database();
		const {name, count, dividendPerShare} = state.company;

		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies/' + name + '/dividendPerShare').set(dividendPerShare);
		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies/' + name + '/count').set(count);

		dispatch({type: SHOW_PRELOADER});

		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').once('value')
			.then(response => {
				dispatch(reRenderCompanies(response))
			}).catch(err => console.error(err));
	}
}

// Удаление компаний
export function deleteCompany(name) {
	return async (dispatch, getState) => {
		const db = firebase.database();

		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies/' + name).remove();
		await db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').once('value')
			.then(response => {
				dispatch({type: SHOW_PRELOADER});

				if (response.val() === null) {
					db.ref('/users/' + getState().authReducer.token + '/portfolio/companies').update({});
					// currency отвечает за рендер таблицы. А чтобы её запустить, нужно,чтобы в companies было изменение тоже
					const currency = [];
					const companies = {};

					dispatch({type: RENDER, currency, companies, showPreloader: false});
					dispatch(closeModal(null, "isOpenModalUpdateCompany"));
					return
				}

				return response;
			})
			.then(response => {
				dispatch(reRenderCompanies(response))
			})
			.catch(err => {
				console.error(err)
			});
	}
}

/*----------START События для инпутов-------------*/

// Событие onChange для инпута. Данные идут в state.company для будущей отправки на сервер
export function getCompanyDataChangeHandler(e, inputName, inputsObj) {
	return (dispatch, getState) => {
		const state = getState().mainReducer;
		const company = {...state.company};
		const Inputs = {...state[inputsObj]};
		// control - это name, count, dividensPerShare
		const control = {...Inputs[inputName]};

		// Валидация
		control.touched = true;
		control.value = e.target.value;
		control.valid = validateControl(control.value, control.validation);

		Inputs[inputName] = control;
		company[inputName] = e.target.value;
		company.currency = "USD"; // Валюта по умолчанию

		let isFormValid = true;

		// Валидация формы (активные кнопки или нет)
		Object.keys(Inputs).forEach(name => {
			isFormValid = Inputs[name].valid && isFormValid;
		});

		dispatch({
			type: GET_COMPANY_DATA,
			company,
			[inputsObj]: Inputs,
			isFormValid
		})
	}
}

//Событие onChange для select
export function selectHandler(e) {
	return (dispatch, getState) => {
		const state = getState().mainReducer;

		const company = {...state.company};
		company.currency = e.target.value;

		dispatch({type: CHANGE_SELECT, company});
	}
}

/*----------END События для инпутов-------------*/

/*----------START Функции закрытия и открытия модального окна-------------*/

// Функция возвращает новую функцию, которая принимает dispatch или getState( и так далее
export function openModal(e, modalName) {
	return (dispatch, getState) => {
		const state = getState().mainReducer;
		const modals = {...state.modals};

		modals[modalName] = true;

		// Либо Я открываю окно для редактирования/удаления
		if (modalName === "isOpenModalUpdateCompany") {
			const nameForDelete = e.target.parentNode.querySelector('span').textContent;
			dispatch(openUpdateModal(modals, nameForDelete));
			return
		}
		// Либо для новой компании
		dispatch(openCreateModal(modals));

		function openUpdateModal(modals, nameForDelete) {
			return {
				type: OPEN_MODAL_UPDATE,
				modals,
				nameForDelete
			}
		}

		function openCreateModal(modals) {
			return {
				type: OPEN_MODAL_UPDATE,
				modals
			}
		}
	}
}

export function closeModal(e, modalName) {
	return (dispatch, getState) => {
		const state = getState().mainReducer;

		const modals = {...state.modals};
		const formAddInputs = {...state.formAddInputs};

		modals[modalName] = false;

		let isFormValid = false;

		Object.keys(formAddInputs).forEach(name => {
			const control = {...formAddInputs[name]};
			control.valid = false;
			control.touched = false;
			control.value = "";
			formAddInputs[name] = control;
		});

		dispatch(closeModal(modals, formAddInputs, isFormValid));

		function closeModal() {
			return {
				type: CLOSE_MODAL,
				modals, formAddInputs, isFormValid
			}
		}

	}
}

/*----------END Функции закрытия и открытия модального окна-------------*/

/* --- Мои функции для сокращения количества кода ---*/

export function reRenderCompanies(response) {
	return dispatch => {
		const companies = response.val();
		const currency = [];

		Object.keys(companies).map(key => {
			if (!currency.includes(companies[key].currency)) {
				currency.push(companies[key].currency)
			}
		});

		dispatch({type: RENDER, currency, companies, showPreloader: false});
		dispatch(closeModal(null, "isOpenModalUpdateCompany"));
	}
}