import React from "react";
import classes from './DepoStats.module.scss'
import {connect} from "react-redux";

const DepoStats = props => {
	const depo = props.deposit;
	let rub = 0;
	let usd = 0;

	function getTotalDepo() {
		Object.keys(depo).map(year => {
			const currencyObj = depo[year];

			Object.keys(currencyObj).map(currency => {
				const currencyArr = currencyObj[currency];

				currencyArr.forEach(obj => {
					currency === 'rub' ? rub += obj.amount : usd += obj.amount
				})
			})
		})
	}

	return (
		<div>
			<h3 className={classes.mainTitle}>Статистика пополнений за все время</h3>
			<div className={classes.totalContainer}>
				{
					getTotalDepo()
				}
				<p className={classes.currencyBox}>
					<span className={classes.rub}>RUB:</span> {rub.toLocaleString('ru')} руб.
				</p>
				<p className={classes.currencyBox}>
					<span className={classes.usd}>USD:</span> {usd.toLocaleString('ru')} $
				</p>
			</div>
		</div>
	)
};

function mapStateToProps(state) {
	return JSON.parse(JSON.stringify(state.depositReducer))
}

export default connect(mapStateToProps)(DepoStats);