import {validateControl} from "../../../Modules/formValidation";
import {
	AUTH_AUTO_LOGIN, AUTH_CLOSE_ERROR,
	AUTH_INPUT,
	AUTH_LOGIN,
	AUTH_LOGOUT, AUTH_SHOW_ERROR,
	DEPOSIT_RENDER,
	MAIN_RENDER,
} from "../../actions/actionsType";
import {renderCompanies} from "../mainReducer/actions";
import firebase from "firebase";

export function loginHandler(isLogin) {
	return async (dispatch, getState) => {
		const state = getState().authReducer;
		const authInputs = {...state.authInputs};
		const inputEmail = {...state.authInputs.inputEmail};
		const inputPassword = {...state.authInputs.inputPassword};
		const email = inputEmail.value;
		const password = inputPassword.value;

		try {
			if (isLogin) {
				await firebase.auth().signInWithEmailAndPassword(email, password);
			} else {
				await firebase.auth().createUserWithEmailAndPassword(email, password);
			}

			// Слушатель firebase
			await firebase.auth().onAuthStateChanged(user => {
				const currentUser = firebase.auth().currentUser;

				//Блок с верификацией email
				if (!currentUser.emailVerified) {
					currentUser.sendEmailVerification()
						.then(() => {
							const message = "Вам на почту было отправлено письмо с подтверждением. Пожалуйста, проверьте свою почту";
							dispatch({type: AUTH_SHOW_ERROR, error: message});

							setTimeout(() => {
								dispatch({type: AUTH_CLOSE_ERROR})
							}, 8500)
						})
						.catch(error => {
							dispatch({type: AUTH_SHOW_ERROR, error: error.message});

							setTimeout(() => {
								dispatch({type: AUTH_CLOSE_ERROR})
							}, 8500)
						});
					return
				}

				const login = {
					uid: user.uid,
					entryTime: +user.metadata.b
				};

				inputEmail.value = inputPassword.value = '';
				authInputs.inputEmail = inputEmail;
				authInputs.inputPassword = inputPassword;

				dispatch({type: AUTH_LOGIN, authInputs, token: login.uid});
				// Использую диспатч, чтобы заново отрендерить компании, функция тянется из mainReducers
				dispatch(renderCompanies());
				localStorage.setItem("login", JSON.stringify(login))
			});
		} catch (e) {
			dispatch(showError(e))
		}


	}
}

export function authForm(e) {
	e.preventDefault();
	return dispatch => {

	}
}

export function authChangeHandler(e, inputObj, inputName) {
	return (dispatch, getState) => {
		const state = getState().authReducer;
		const authInputs = {...state[inputObj]};
		const input = {...authInputs[inputName]};

		input.touched = true;
		input.value = e.target.value;
		input.valid = validateControl(input.value, input.validation);

		authInputs[inputName] = input;

		let isFormValid = true;
		// Валидация формы (активные кнопки или нет)

		Object.keys(authInputs).forEach(name => {
			isFormValid = authInputs[name].valid && isFormValid;
		});

		dispatch({type: AUTH_INPUT, [inputObj]: authInputs, isFormValid});
	}
}

export function autoLogin() {
	return dispatch => {
		const login = JSON.parse(localStorage.getItem("login"));

		if (login === null) {
			dispatch({type: AUTH_AUTO_LOGIN, token: null})
		} else {
			dispatch({type: AUTH_AUTO_LOGIN, token: login.uid})
		}
	}
}

export function logout(e) {
	e.preventDefault();
	return dispatch => {
		firebase.auth().signOut().then(() => {
			const currency = [];
			const deposit = {};
			// Тут Я очищаю localStorage, так же для депозитов и валют Я задаю пустые значения, чтобы при перелогине, мы заново их заполняли согласно ключу. Не знаю, насколько это правильно
			localStorage.removeItem("login");
			dispatch({type: AUTH_LOGOUT});
			dispatch({type: MAIN_RENDER, currency});
			dispatch({type: DEPOSIT_RENDER, deposit});
		}).catch(err => {
			dispatch({type: AUTH_SHOW_ERROR, error: err.message});

			setTimeout(() => {
				dispatch({type: AUTH_CLOSE_ERROR})
			}, 8500)
		});
	}
}

export function showError(e) {
	return dispatch => {
		dispatch({type: AUTH_SHOW_ERROR, error: e.message});

		setTimeout(() => {
			dispatch({type: AUTH_CLOSE_ERROR})
		}, 8500)
	}
}