import React from "react";
import ReactDOM from "react-dom";
import "./index.scss";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import {BrowserRouter} from "react-router-dom";
// redux
import {createStore, compose, applyMiddleware} from "redux"
import {Provider} from "react-redux"
import rootReducer from "./redux/reducers/rootReducers";
import thunk from "redux-thunk";

// Firebase App (the core Firebase SDK) is always required and must be listed first
import firebase from "firebase/app";

// Your web app's Firebase configuration
const firebaseConfig = {
	apiKey: "AIzaSyB73IC7XvE6QjjTcMWXZNePBcn_uiLgqWA",
	authDomain: "my-finance-journal.firebaseapp.com",
	databaseURL: "https://my-finance-journal.firebaseio.com",
	projectId: "my-finance-journal",
	storageBucket: "my-finance-journal.appspot.com",
	messagingSenderId: "152785363240",
	appId: "1:152785363240:web:e998b6a6a0fcacfb02701d"
};
// Initialize Firebase
firebase.initializeApp(firebaseConfig);


// Настройка redux devtools
const composeEnhancers =
	typeof window === 'object' &&
	window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ ?
		window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({}) : compose;

// Создаем общий store, в него мы передаем reducer
//reducer - это просто функция, которая меняет state через dispatch
const store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)));

// Обернул все приложение в Provider, чтобы связать redux и react. Передал общий store, теперь он доступен через props

const app = (
	<Provider store={store}>
		<BrowserRouter>
			<App/>
		</BrowserRouter>
	</Provider>
);

ReactDOM.render(app, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
